import {Box} from 'components';
import {Child, TCard} from './card.type';
import {CardStyled} from './styled';

export const Card: TCard = ({children, ...props}) => (
  <CardStyled {...props}>{children}</CardStyled>
);

const Header: Child = ({children, ...props}) => (
  <Box className="card_header" component={'header'} width={1} {...props}>
    {children}
  </Box>
);

const Body: Child = ({children, ...props}) => (
  <Box className="card_body" width={1} {...props}>
    {children}
  </Box>
);

const Action: Child = ({children, ...props}) => (
  <Box className="card_action" component={'footer'} width={1} {...props}>
    {children}
  </Box>
);

Card.Header = Header;
Card.Body = Body;
Card.Action = Action;
