import {Box} from 'components';
import {useEffect, useRef, useCallback, useState} from 'react';
import {createPortal} from 'react-dom';
import {ModalStyled} from './styled';
import {TModal} from './modal.type';

export const Modal: TModal = ({
  open,
  onClose,
  onBackdropClick,
  children,
  closeable = true,
  rootId = 'root',
}) => {
  const modalRoot = useRef<HTMLDivElement | null>(null);

  const [render, setRender] = useState<boolean>(false);
  const [innerOpen, setInnerOpen] = useState<boolean>(false);

  useEffect(() => {
    modalRoot.current = document.createElement('div');
    document.getElementById(rootId)?.appendChild(modalRoot.current);

    return () => {
      if (modalRoot.current) {
        document.getElementById(rootId)?.removeChild(modalRoot.current);
      }
    };
  }, [rootId]);

  useEffect(() => {
    let timeout: ReturnType<typeof setTimeout>;

    if (open) {
      setRender(true);
      timeout = setTimeout(() => {
        setInnerOpen(true);
      }, 300);
    } else {
      setInnerOpen(false);
      timeout = setTimeout(() => {
        setRender(false);
      }, 300);
    }

    return () => {
      if (timeout) clearTimeout(timeout);
    };
  }, [open]);

  const handleClose = useCallback(() => {
    if (onBackdropClick) {
      onBackdropClick();
    } else if (closeable) {
      onClose();
    }
  }, [closeable, onBackdropClick, onClose]);

  return render && modalRoot.current
    ? createPortal(
        <ModalStyled opacity={innerOpen ? 1 : 0} onClick={handleClose}>
          <Box
            opacity={innerOpen ? 1 : 0}
            className="modal_content"
            onClick={e => e.stopPropagation()}
          >
            {children}
          </Box>
        </ModalStyled>,
        modalRoot.current,
      )
    : null;
};
