import {deepMerge} from '../../utils/deepMerge';
import {colors, THEME} from '../provider/config';
import {TTheme, TProviderProps} from '../provider/TProvider.type';
import {useMemo} from 'react';

export const useConfigTheme = ({
  config,
  mode = 'dark',
}: TProviderProps): {theme: TTheme} => {
  // IMPLEMENTATION OF DEEP MERGE
  const finalTheme = useMemo(() => {
    if (config) {
      return deepMerge(THEME, config);
    } else {
      return THEME;
    }
  }, [config]);

  const theme = useMemo<TTheme>(() => {
    finalTheme.colors = colors[mode];
    return finalTheme;
  }, [mode, finalTheme]);

  return {theme};
};
