import {Global, ThemeProvider, css} from '@emotion/react';
import {TProvider} from './TProvider.type';
import {useConfigTheme} from '../hooks/useConfigTheme';
import {ModalContextProvider} from '@core/contexts/modal';

export const Provider: TProvider = ({config, mode = 'dark', children}) => {
  const {theme} = useConfigTheme({config, mode});
  return (
    <ThemeProvider theme={theme}>
      <Global
        styles={css`
          * {
            box-sizing: border-box;
            direction: rtl;
          }
        `}
      />
      <ModalContextProvider>{children}</ModalContextProvider>
    </ThemeProvider>
  );
};
