import {Button, Card, Modal} from 'components';
import React, {useCallback} from 'react';
import {TInnerPopUp, TModalContext, TModalProvider} from './type';

export const ModalContext = React.createContext<TModalContext>(
  {} as TModalContext,
);

export const ModalContextProvider: TModalProvider = ({children}) => {
  const [modals, setModals] = React.useState<TInnerPopUp[]>([]);

  const showPopUp: TModalContext['showPopUp'] = useCallback(
    ({title, message, options}) => {
      const key = options?.key ?? new Date().getTime();

      const newModal = {
        title,
        message,
        options: {
          key,
          open: true,
          ...(options && options),
        },
      };

      setModals(prev => [...prev, newModal]);

      return key;
    },
    [],
  );

  const hidePopUp: TModalContext['hidePopUp'] = useCallback(key => {
    setModals(prev =>
      prev.map(modal => {
        if (modal.options.key === key) {
          return {...modal, options: {...modal.options, open: false}};
        } else {
          return modal;
        }
      }),
    );

    setTimeout(() => {
      setModals(prev => prev.filter(modal => modal.options.open));
    }, 1000);
  }, []);

  return (
    <ModalContext.Provider value={{showPopUp, hidePopUp}}>
      {modals.map(modal => (
        <Modal
          key={modal.options.key}
          open={modal.options.open}
          onClose={() => hidePopUp(modal.options.key)}
        >
          <Card>
            <Card.Header>{modal.title}</Card.Header>
            <Card.Body>{modal.message}</Card.Body>
            <Card.Action>
              {modal.options?.showSuccessButton && (
                <Button onClick={modal.options?.acceptButton?.fn}>
                  {modal.options?.acceptButton?.text || ''}
                </Button>
              )}
              {modal.options?.showAlertButton && (
                <Button onClick={modal.options?.declineButton?.fn}>
                  {modal.options?.declineButton?.text || ''}
                </Button>
              )}
            </Card.Action>
          </Card>
        </Modal>
      ))}
      {children}
    </ModalContext.Provider>
  );
};
